.infoBox{
    flex: 1;
    cursor: pointer;
}
.infoBox:not(:last-child){
    margin-right: 20px;
}

.infoBox-selected{
    border-top: 3px solid greenyellow;
}
.infoBox-red {
    border-color: red;
}

.infoBox_cases-green{
    color: lightgreen !important;
}
.infoBox_cases{
    color: #cc1034;
    font-weight: 600;
    font-size: 1.75rem;
    margin-bottom: 0.5rem;
}
.infoBox_total{
    color: #6C757d;
    font-weight: 700 !important;
    font-size: 0.8rem !important;
    margin-top: 20px !important;
}