*{
  margin: 0;
}
body{
  background-color: #f5f6fa;
}
.app{
  display: flex;
  justify-content: space-between;
  padding: 20px;
 
}
.app_dropdown{
  background-color: white;
}
.app_header > h1 {
  color: black;
  font-size: 2rem;
}
.app_header {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
  justify-content:space-between;
}
.app_left{
  float: 0.9;
}
.app_stats {
  display: flex;
  justify-content:space-between;
}
.app_right{
  display: flex;
  flex-direction: column;
 
}
.app_right .MuiCardContent-root{
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  
}
.app_graph {
  flex-grow: 1;
}

.app_graphTitle{
  margin-top: 30px;
  margin-bottom: 20px;
}
@media  only screen and (max-width: 1200px) {
  .app {
    flex-direction: row;
    
  }
}
@media  only screen and (max-width: 992px) {
  .app {
    flex-direction: row;
    
  }
}
@media  only screen and (max-width: 768px) {
  .app {
    flex-direction:column;
  }
}
@media  only screen and (max-width: 576px) {
  .app {
    flex-direction: column;
  }
}